/* Container */
.contact-container {
    max-width: 600px;
    margin: 2rem auto;
    background: var(--white);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

/* Header */
.contact-header h1 {
    font-size: 2.5rem;
    color: var(--blue);
    margin-bottom: 0.5rem;
}

.contact-header p {
    font-size: 1.1rem;
    color: var(--dark-grey);
    margin-bottom: 2rem;
}

/* Form Styling */
.contact-form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    text-align: left;
}

.contact-form label {
    font-size: 1rem;
    color: var(--blue);
}

.contact-form input,
.contact-form textarea {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid var(--dark-grey);
    border-radius: 5px;
    font-size: 1rem;
    color: var(--blue);
    background-color: var(--light-grey);
    transition: border-color 0.3s;
}

.contact-form input:focus,
.contact-form textarea:focus {
    border-color: var(--blue);
    outline: none;
}

.contact-form textarea {
    resize: vertical;
    min-height: 100px;
}

/* Button */
.button {
    display: inline-block;
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background-color: var(--blue);
    color: var(--white);
    text-align: center;
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s, transform 0.3s;
}

.button:hover {
    background-color: var(--yellow);
    color: var(--blue);
    transform: scale(1.1);
}

/* Thank You Message */
.thank-you h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 0.5rem;
}

.thank-you p {
    font-size: 1.1rem;
    color: var(--dark-grey);
    margin-bottom: 1.5rem;
}

.error {
    color: red;
    margin-top: 1rem;
    font-size: 1rem;
    text-align: center;
}