/* Header Container */
.header {
  background-color: var(--blue);
  color: var(--white);
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column; /* Stack elements vertically */
  align-items: center;
  text-align: center;
}

/* Logo Section (h1) */
.header .logo h1 {
  font-size: 2rem;
  color: var(--white);
  margin-bottom: 1rem; /* Space between h1 and navigation */
}

/* Navigation */
.navigation {
  display: flex;
  gap: 1.5rem; /* Space between links */
}

.navigation a {
  color: var(--white);
  text-decoration: none;
  font-weight: bold;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

.navigation a:hover {
  background-color: var(--yellow);
  color: var(--blue);
}

