/* Programmer Page Container */
.programmer-container {
    max-width: 900px;
    margin: 2rem auto;
    background: var(--white);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

/* Header Section */
.programmer-header h1 {
    font-size: 2.5rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.programmer-header p {
    font-size: 1.2rem;
    color: var(--dark-grey);
    margin-bottom: 2rem;
    line-height: 1.6;
}

/* Skills Section */
.programmer-skills h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.programmer-skills ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    text-align: left;
    color: var(--dark-grey);
}

.programmer-skills ul li {
    font-size: 1rem;
    margin-bottom: 0.5rem;
    padding-left: 1.5rem;
    position: relative;
}

.programmer-skills ul li:before {
    content: "•";
    position: absolute;
    left: 0;
    color: var(--blue);
    font-weight: bold;
}

/* Projects Section */
.programmer-projects {
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.programmer-projects h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 1.5rem;
}

.project-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
}

.card {
    background: var(--light-grey);
    border-radius: 8px;
    padding: 1.5rem;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
    text-align: left;
}

.card:hover {
    transform: scale(1.05);
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

.card h3 {
    font-size: 1.5rem;
    color: var(--blue);
    margin-bottom: 0.5rem;
}

.card p {
    font-size: 1rem;
    color: var(--dark-grey);
    margin-bottom: 1rem;
}

.card .button {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
    background-color: var(--blue);
    color: var(--white);
    text-transform: uppercase;
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s, transform 0.3s;
}

.card .button:hover {
    background-color: var(--yellow);
    color: var(--blue);
    transform: scale(1.1);
}

/* Call-to-Action Section */
.programmer-call-to-action {
    text-align: center;
    margin-top: 2rem;
}

.programmer-call-to-action h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.programmer-call-to-action p {
    font-size: 1.1rem;
    color: var(--dark-grey);
    margin-bottom: 1.5rem;
}

.programmer-call-to-action .button {
    font-size: 1rem;
    padding: 0.75rem 1.5rem;
    background-color: var(--blue);
    color: var(--white);
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s, transform 0.3s;
}

.programmer-call-to-action .button:hover {
    background-color: var(--yellow);
    color: var(--blue);
    transform: scale(1.1);
}
