/* Blog Container */
.blog-container {
    max-width: 900px;
    margin: 2rem auto;
    background: var(--white);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

/* Header Styling */
.blog-header {
    text-align: center;
    margin-bottom: 2rem;
}

.blog-header h1 {
    font-size: 2.5rem;
    color: var(--blue);
    margin-bottom: 0.5rem;
}

.blog-header p {
    font-size: 1.1rem;
    color: var(--dark-grey);
}

/* Blog Posts */
.blog-posts {
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
}

/* Blog Post Card */
.blog-post {
    background-color: var(--light-grey);
    border-radius: 8px;
    padding: 1.5rem;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
}

.blog-post h2 {
    font-size: 1.5rem;
    color: var(--blue);
    margin-bottom: 0.5rem;
}

.blog-post p {
    font-size: 1rem;
    color: var(--dark-grey);
    margin-bottom: 1rem;
}

.blog-post .button {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
    background-color: var(--blue);
    color: var(--white);
    text-transform: uppercase;
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s, transform 0.3s;
}

.blog-post .button:hover {
    background-color: var(--yellow);
    color: var(--blue);
    transform: scale(1.1);
}

/* Hover Effect */
.blog-post:hover {
    transform: scale(1.02);
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

/* Responsive Design */
@media (min-width: 768px) {
    .blog-posts {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 1024px) {
    .blog-posts {
        grid-template-columns: repeat(3, 1fr);
    }
}
