/* FAQs Container */
.faqs-container {
    max-width: 800px;
    margin: 2rem auto;
    background: var(--white);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

/* Header */
.faqs-header h1 {
    font-size: 2.5rem;
    color: var(--blue);
    margin-bottom: 0.5rem;
}

.faqs-header p {
    font-size: 1.1rem;
    color: var(--dark-grey);
    margin-bottom: 2rem;
}

/* FAQ Items */
.faqs-list {
    text-align: left;
}

.faq-item {
    margin-bottom: 1.5rem;
    padding: 1rem;
    border-radius: 8px;
    background-color: var(--light-grey);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: transform 0.3s, box-shadow 0.3s;
}

.faq-item:hover {
    transform: scale(1.02);
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

/* Question Styling */
.faq-question {
    font-size: 1.2rem;
    font-weight: bold;
    color: var(--blue);
    margin-bottom: 0.5rem;
}

/* Answer Styling */
.faq-answer {
    font-size: 1rem;
    color: var(--dark-grey);
    display: none;
    line-height: 1.6;
    margin-top: 0.5rem;
}

.faq-answer.show {
    display: block;
}

/* Active FAQ */
.faq-item.active .faq-question {
    color: var(--yellow);
}
