/* General Styling for the App */
.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  background-color: var(--light-grey);
  color: var(--blue);
}

/* Main Content Area */
.content {
  flex: 1;
  padding: 2rem;
}

/* Reset Styles for Common Elements */
body, h1, h2, h3, p, ul, li, a {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}

/* Global Color Variables */
:root {
  --blue: #1E3A8A; /* Navy Blue */
  --yellow: #FFD700; /* Yellow */
  --white: #ffffff; /* White */
  --light-grey: #f8f9fa; /* Light Grey Background */
  --dark-grey: #374151; /* Dark Grey */
}

/* Anchor Link Styling */
a {
  color: var(--blue);
  transition: color 0.3s;
}

a:hover {
  color: var(--yellow);
}