/* Footer container styling */
.footer {
    background-color: #00008b; /* Navy blue */
    color: white;
    padding: 20px;
    text-align: center;
    margin-top: 20px;
    border-top: 5px solid #ffd700; /* Yellow border */
}

  /* Footer content layout */
.footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

  /* Footer links */
.footer-links {
    display: flex;
    gap: 15px;
}

.footer-links a {
    color: #ffd700; /* Yellow */
    text-decoration: none;
    transition: color 0.3s, text-shadow 0.3s;
}

.footer-links a:hover {
    color: white;
    text-shadow: 0px 0px 5px #ffd700; /* Glow effect */
} 

/* Social media icons container */
.footer-socials {
    margin-top: 10px;
    display: flex;
    justify-content: center;
    gap: 15px;
}

  /* Individual social media icons */
.footer-socials a {
    color: #ffd700; /* Yellow */
    font-size: 1.8rem;
    transition: color 0.3s, transform 0.3s;
}

.footer-socials a:hover {
    color: white;
    transform: scale(1.2); /* Slight zoom on hover */
}