/* General Styling */
:root {
    --blue: #1E3A8A; /* Navy Blue */
    --yellow: #FFD700; /* Yellow */
    --white: #ffffff; /* White */
    --light-grey: #f8f9fa;
    --dark-grey: #374151; /* Darker grey */
}

body {
    background-color: var(--light-grey);
    color: var(--blue);
    font-family: Arial, sans-serif;
    line-height: 1.6;
}

a {
    text-decoration: none;
    color: var(--blue);
    transition: color 0.3s, background-color 0.3s;
}

a:hover {
    color: var(--yellow);
}

/* Header */
.header {
    background-color: var(--blue);
    color: var(--white);
    padding: 1rem 2rem;
    position: relative;
    text-align: center;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.header h1 {
    font-size: 2rem;
}

.menu-toggle {
    display: none;
    font-size: 1.5rem;
    color: var(--white);
    background: none;
    border: none;
    cursor: pointer;
    position: absolute;
    top: 1rem;
    right: 2rem;
}

/* Navigation */
.nav-menu {
    display: flex;
    justify-content: center;
    gap: 1.5rem;
    margin-top: 1rem;
    flex-wrap: wrap;
}

.nav-menu a {
    font-weight: bold;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    transition: color 0.3s, background-color 0.3s;
}

.nav-menu a:hover {
    background-color: var(--yellow);
    color: var(--white);
}

/* Responsive Menu Toggle for Mobile */
@media (max-width: 768px) {
    .menu-toggle {
        display: block;
    }

    .nav-menu {
        display: none;
        flex-direction: column;
        text-align: center;
    }

    .nav-menu.active {
        display: flex;
    }
}

/* Main Content */
.pages {
    max-width: 900px;
    margin: 2rem auto;
    background: var(--white);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

/* Spacing Between Sections */
.pages section {
    margin-bottom: 2.5rem;
}

/* Button Styling */
.button {
    display: inline-block;
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background-color: var(--blue);
    color: var(--white);
    text-align: center;
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s, transform 0.3s;
}

.button:hover {
    background-color: var(--yellow);
    color: var(--blue);
    transform: scale(1.1);
}
