/* Influencer Page Container */
.influencer-container {
    max-width: 900px;
    margin: 2rem auto;
    background: var(--white);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

/* Header Section */
.influencer-header h1 {
    font-size: 2.5rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.influencer-header p {
    font-size: 1.2rem;
    color: var(--dark-grey);
    margin-bottom: 2rem;
    line-height: 1.6;
}

/* Platforms Section */
.influencer-platforms h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.influencer-platforms ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    text-align: left;
    color: var(--dark-grey);
}

.influencer-platforms ul li {
    font-size: 1rem;
    margin-bottom: 1rem;
}

.influencer-platforms ul li a {
    color: var(--blue);
    font-weight: bold;
    text-decoration: none;
    transition: color 0.3s;
}

.influencer-platforms ul li a:hover {
    color: var(--yellow);
}

/* Collaborations Section */
.influencer-collaborations {
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.influencer-collaborations h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.influencer-collaborations p {
    font-size: 1rem;
    color: var(--dark-grey);
    line-height: 1.6;
}

/* Call-to-Action Section */
.influencer-call-to-action {
    text-align: center;
    margin-top: 2rem;
}

.influencer-call-to-action h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.influencer-call-to-action p {
    font-size: 1.1rem;
    color: var(--dark-grey);
    margin-bottom: 1.5rem;
}

.influencer-call-to-action .button {
    font-size: 1rem;
    padding: 0.75rem 1.5rem;
    background-color: var(--blue);
    color: var(--white);
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s, transform 0.3s;
}

.influencer-call-to-action .button:hover {
    background-color: var(--yellow);
    color: var(--blue);
    transform: scale(1.1);
}
