/* Genealogist Page Container */
.genealogist-container {
    max-width: 900px;
    margin: 2rem auto;
    background: var(--white);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

/* Header */
.genealogist-header h1 {
    font-size: 2.5rem;
    color: var(--blue);
    margin-bottom: 0.5rem;
}

.genealogist-header p {
    font-size: 1.1rem;
    color: var(--dark-grey);
    margin-bottom: 2rem;
}

/* Services Section */
.genealogist-services {
    text-align: left;
    margin-bottom: 2rem;
}

.genealogist-services h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.genealogist-services ul {
    list-style-type: disc;
    padding-left: 1.5rem;
    color: var(--dark-grey);
}

.genealogist-services ul li {
    font-size: 1rem;
    margin-bottom: 0.5rem;
}

/* About Section */
.genealogist-about {
    margin-bottom: 2rem;
}

.genealogist-about h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.genealogist-about p {
    font-size: 1rem;
    color: var(--dark-grey);
    line-height: 1.6;
}

/* Call to Action Section */
.genealogist-call-to-action h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.genealogist-call-to-action p {
    font-size: 1.1rem;
    color: var(--dark-grey);
    margin-bottom: 1.5rem;
}

/* Button Styling */
.button {
    display: inline-block;
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background-color: var(--blue);
    color: var(--white);
    text-align: center;
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s, transform 0.3s;
}

.button:hover {
    background-color: var(--yellow);
    color: var(--blue);
    transform: scale(1.1);
}
