/* Home Page Container */
.home-container {
    max-width: 1200px;
    margin: 2rem auto;
    padding: 2rem;
    background: var(--white);
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

/* Header Section */
.home-header h1 {
    font-size: 2.5rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.home-header p {
    font-size: 1.2rem;
    color: var(--dark-grey);
    margin-bottom: 2rem;
    line-height: 1.6;
}

/* Highlights Section */
.home-highlights h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 1.5rem;
}

.highlight-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
    margin-top: 1rem;
}

.card {
    background: var(--light-grey);
    border-radius: 8px;
    padding: 1.5rem;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
    text-align: left;
}

.card:hover {
    transform: scale(1.05);
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

.card h3 {
    font-size: 1.5rem;
    color: var(--blue);
    margin-bottom: 0.5rem;
}

.card p {
    font-size: 1rem;
    color: var(--dark-grey);
    margin-bottom: 1rem;
}

.card .button {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
    background-color: var(--blue);
    color: var(--white);
    text-transform: uppercase;
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s, transform 0.3s;
}

.card .button:hover {
    background-color: var(--yellow);
    color: var(--blue);
    transform: scale(1.1);
}

/* Call-to-Action Section */
.home-call-to-action {
    margin-top: 2rem;
    text-align: center;
}

.home-call-to-action h2 {
    font-size: 2rem;
    color: var(--blue);
    margin-bottom: 1rem;
}

.home-call-to-action p {
    font-size: 1.1rem;
    color: var(--dark-grey);
    margin-bottom: 1.5rem;
}

.home-call-to-action .button {
    font-size: 1rem;
    padding: 0.75rem 1.5rem;
    background-color: var(--blue);
    color: var(--white);
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s, transform 0.3s;
}

.home-call-to-action .button:hover {
    background-color: var(--yellow);
    color: var(--blue);
    transform: scale(1.1);
}
